.input {
    display: flex;
    width: 300px;
    font-size: 13px;
    border: 3px solid #CADFCF;
    padding: 0.6em;
    border-radius: 4px;
    background: #FEFEFE;
    color: #231F20;
    margin: 15px;
}

.label {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    flex-direction: column;
}