.list {
    margin-left: 0;
    list-style: none;
    counter-reset: li;
    padding: 0;
}

.item {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 1.5em;
    border: 3px solid #CADFCF;
    padding: 0.6em;
    border-radius: 4px;
    background: #fefefea4;
    color: #231F20;
}

.item:before {
    position: absolute;
    top: -0.7em;
    padding-left: 0.4em;
    padding-right: 0.4em;
    font-size: 16px;
    font-weight: bold;
    color: #728d78;
    background: #fefefe;
    border-radius: 50%;
    counter-increment: li;
    content: counter(li);
}

.name {
    font-weight: 500;
    padding-right: 15px;
}

.number {
    padding-right: 15px;
}

.button {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 45px;
    line-height: 45px;
    border: 3px solid #CADFCF;
    padding: 0.6em;
    border-radius: 4px;
    margin: 10px 20px;
    /* text-transform: uppercase; */
    text-align: center;
    letter-spacing: 3px;
    font-weight: 600;
    color: #524f4e;
    background: white;
    box-shadow: 0 8px 15px rgba(0, 0, 0, .1);
    transition: .3s;
}

.button:hover {
    background: #728d78;
    box-shadow: 0 15px 20px #728d78;
    color: white;
    transform: translateY(-7px);
    border: none;
}